import { create } from "zustand";
import type { FormSchema } from "@shadcn-builder/renderer";

interface DesignerState {
  schema: FormSchema;
  selectedId: string | null;
  viewport: "sm" | "md" | "lg";
  history: { stack: FormSchema[]; idx: number; max: number };
  setSchema: (s: FormSchema, pushHistory?: boolean) => void;
  select: (id: string | null) => void;
  undo: () => void;
  redo: () => void;
}

export const useDesignerStore = create<DesignerState>((set, get) => ({
  schema: { components: [] },
  selectedId: null,
  viewport: "sm",
  history: { stack: [], idx: -1, max: 100 },
  setSchema: (s, push = true) => {
    if (push) {
      const { history } = get();
      const nextStack = [s, ...history.stack.slice(Math.max(0, history.idx))].slice(0, history.max);
      set({ schema: s, history: { stack: nextStack, idx: 0, max: history.max } });
      return;
    }
    set({ schema: s });
  },
  select: (id) => set({ selectedId: id }),
  undo: () => {
    const { history } = get();
    if (history.idx < history.stack.length - 1) {
      const idx = history.idx + 1;
      set({ schema: history.stack[idx], history: { ...history, idx } });
    }
  },
  redo: () => {
    const { history } = get();
    if (history.idx > 0) {
      const idx = history.idx - 1;
      set({ schema: history.stack[idx], history: { ...history, idx } });
    }
  },
}));
