"use client";
import React, { useMemo } from "react";
import { Renderer, type FormSchema, type ComponentNode } from "@shadcn-builder/renderer";
import { useDesignerStore } from "./state/store";
import {
  DndContext,
  PointerSensor,
  useSensor,
  closestCenter,
  DragEndEvent,
} from "@dnd-kit/core";
import {
  SortableContext,
  verticalListSortingStrategy,
  useSortable,
  arrayMove,
} from "@dnd-kit/sortable";
import { CSS } from "@dnd-kit/utilities";
import { computeRows, GRID_COLUMNS } from "./utils/grid";

const viewportEditorStyles: Record<"sm" | "md" | "lg", string> = {
  sm: "w-[370px]",
  md: "w-[818px]",
  lg: "w-[1074px]",
};

function getSpan(node: ComponentNode): number {
  const raw = (node.properties as any)?.style?.colSpan;
  const n = typeof raw === "string" ? parseInt(raw, 10) : typeof raw === "number" ? raw : undefined;
  if (!n || Number.isNaN(n)) return GRID_COLUMNS;
  return Math.max(1, Math.min(GRID_COLUMNS, n));
}

function SortableItem({ id, children, selected, onClick }: { id: string; children: React.ReactNode; selected: boolean; onClick: () => void; }) {
  const { attributes, listeners, setNodeRef, transform, transition } = useSortable({ id });
  const style: React.CSSProperties = {
    transform: CSS.Transform.toString(transform),
    transition,
  };
  return (
    <div ref={setNodeRef} style={style} {...attributes} {...listeners} onClick={onClick} className={`rounded ${selected ? "ring-2 ring-blue-500" : ""}`}>
      {children}
    </div>
  );
}

export function Canvas({ registry, viewport }: { registry: any; viewport: "sm" | "md" | "lg" }) {
  const schema = useDesignerStore((s) => s.schema);
  const setSchema = useDesignerStore((s) => s.setSchema);
  const selectedId = useDesignerStore((s) => s.selectedId);
  const select = useDesignerStore((s) => s.select);

  const ids = useMemo(() => schema.components.map((n) => n.id), [schema.components]);
  const rows = useMemo(() => computeRows(schema.components), [schema.components]);

  const pointer = useSensor(PointerSensor, { activationConstraint: { distance: 8 } });
  const sensors = useMemo(() => [pointer], [pointer]);

  const onDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;
    if (!over || active.id === over.id) return;
    const oldIndex = ids.indexOf(active.id as string);
    const newIndex = ids.indexOf(over.id as string);
    // 先移动，再按行重新分配 colSpan
    const moved = arrayMove(schema.components, oldIndex, newIndex);
    const flat: ComponentNode[] = [];
    computeRows(moved).forEach((row) => {
      const base = Math.max(1, Math.floor(GRID_COLUMNS / row.length));
      const remainder = GRID_COLUMNS - base * row.length;
      row.forEach((n, idx) => {
        const span = base + (idx < remainder ? 1 : 0);
        const style = { ...((n.properties as any)?.style || {}), colSpan: String(span) };
        flat.push({ ...n, properties: { ...(n.properties || {}), style } } as ComponentNode);
      });
    });
    setSchema({ ...schema, components: flat });
  };

  return (
    <div className="px-4 pb-10">
      <div className={`mx-auto ${viewportEditorStyles[viewport]} bg-white border rounded-md shadow-sm p-4`}>
        <div className="mb-2 text-sm text-slate-500">拖拽条目可重排；按行自动均分 colSpan；多于一行将自动换行。</div>
        <DndContext sensors={sensors} collisionDetection={closestCenter} onDragEnd={onDragEnd}>
          <SortableContext items={ids} strategy={verticalListSortingStrategy}>
            <div className="space-y-4">
              {rows.map((row, rIndex) => (
                <div key={`row-${rIndex}`} style={{ display: "grid", gridTemplateColumns: `repeat(${GRID_COLUMNS}, minmax(0, 1fr))`, gap: 12 }}>
                  {row.map((n) => {
                    const span = getSpan(n);
                    return (
                      <div key={n.id} style={{ gridColumn: `span ${span} / span ${span}` }} className="border rounded p-2 bg-white">
                        <SortableItem id={n.id} selected={selectedId === n.id} onClick={() => select(n.id)}>
                          <div className="flex items-center gap-2 mb-2">
                            <span className="text-xs text-slate-500">{n.type}</span>
                          </div>
                          <Renderer schema={{ components: [n] } as FormSchema} registry={registry} context={{ viewport }} />
                        </SortableItem>
                      </div>
                    );
                  })}
                </div>
              ))}
            </div>
          </SortableContext>
        </DndContext>
      </div>
    </div>
  );
}
