"use client";
import React from "react";
import { useDesignerStore } from "../state/store";
import type { FormSchema, ComponentNode } from "@shadcn-builder/renderer";

function generateId(prefix: string, schema: FormSchema) {
  let i = 1;
  while (schema.components.some((c) => c.id === `${prefix}-${i}`)) i++;
  return `${prefix}-${i}`;
}

export function LeftPanel() {
  const schema = useDesignerStore((s) => s.schema);
  const setSchema = useDesignerStore((s) => s.setSchema);

  const addComponent = (node: ComponentNode) => {
    const withId = { ...node, id: generateId(node.type, schema) };
    setSchema({ ...schema, components: [...schema.components, withId] });
  };

  return (
    <div>
      <h3 className="font-medium mb-2">物料</h3>
      <div className="space-y-2">
        <button className="border px-2 py-1 rounded" onClick={() => addComponent({ id: "-", type: "text", properties: { text: "新文本" } })}>文本</button>
        <button className="border px-2 py-1 rounded" onClick={() => addComponent({ id: "-", type: "input", attributes: { placeholder: "请输入" } })}>输入框</button>
      </div>
    </div>
  );
}
