"use client";
import React, { useEffect } from "react";
import { ComponentRegistry, type FormSchema } from "@shadcn-builder/renderer";
import { useDesignerStore } from "./state/store";
import { LeftPanel } from "./panels/LeftPanel";
import { RightPanel } from "./panels/RightPanel";
import { Canvas } from "./Canvas";

export function Designer({ value, defaultValue, onChange, registry, viewport = "sm", showJSON }: {
  value?: FormSchema;
  defaultValue?: FormSchema;
  onChange?: (next: FormSchema) => void;
  registry: ComponentRegistry;
  viewport?: "sm" | "md" | "lg";
  showJSON?: boolean;
}) {
  const setSchema = useDesignerStore((s) => s.setSchema);
  const schema = useDesignerStore((s) => s.schema);

  const isControlled = value !== undefined;

  // Sync from props into internal store
  useEffect(() => {
    if (isControlled && value) {
      if (schema !== value) setSchema(value, false);
    } else if (!isControlled && defaultValue) {
      if (schema !== defaultValue) setSchema(defaultValue, false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isControlled, value, defaultValue]);

  // Emit changes only when uncontrolled
  useEffect(() => {
    if (!isControlled && onChange) onChange(schema);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [schema, isControlled]);

  return (
    <div className="flex flex-col w-full h-full">
      {/* Header */}
      <div className="h-14 border-b flex items-center px-4 shrink-0 bg-white/80 backdrop-blur supports-[backdrop-filter]:bg-white/60">
        <div className="font-medium">Form Builder</div>
        <div className="ml-auto text-sm text-slate-500">Designer</div>
      </div>

      {/* Main 3 columns */}
      <div className="flex w-full h-[calc(100vh-56px)] bg-dotted">
        {/* Left Sidebar */}
        <aside className="w-[300px] border-r p-3 shrink-0 overflow-auto hidden md:block">
          <LeftPanel />
        </aside>

        {/* Canvas */}
        <main className="flex-1 overflow-auto relative pt-6">
          <Canvas registry={registry} viewport={viewport} />
        </main>

        {/* Right Sidebar */}
        <aside className="w-[320px] border-l p-3 shrink-0 overflow-auto hidden md:block">
          <RightPanel />
        </aside>
      </div>

      {showJSON && (
        <pre className="fixed right-0 bottom-0 w-[420px] h-[50vh] bg-white/95 p-3 overflow-auto shadow-xl border">
          {JSON.stringify(schema, null, 2)}
        </pre>
      )}
    </div>
  );
}
